'use client';
import React, { useState } from 'react';

export default function Home() {
  const [text, setText] = useState('');
  const [encoded, setEncoded] = useState('');

  const handleEncode = async () => {
    try {
      const res = await fetch('http://localhost:8000/encode', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }),
      });
      const data = await res.json();
      setEncoded(data.encoded);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <main className="flex flex-col items-center justify-center min-h-screen gap-6 p-6">
      {/* Navigation tabs */}
      <div className="flex gap-4">
        <Link href="/" className="px-4 py-2 bg-gray-200 rounded hover:bg-gray-300">
          Home
        </Link>
        <Link href="/about" className="px-4 py-2 bg-gray-200 rounded hover:bg-gray-300">
          About
        </Link>
      </div>
      <h1 className="text-2xl font-semibold">Welcome to Emily's Enigma Emulator</h1>

      <div className="max-w-md w-full bg-black-100 p-6 rounded-2xl shadow flex flex-col gap-4">
        <h2 className="text-lg font-semibold">Your message...</h2>
        <textarea
          value={text}
          onChange={(e) => setText(e.target.value)}
          rows={6}
          className="w-full border rounded p-3 text-sm resize-vertical"
          placeholder="Type here..."
        />
        <button
          onClick={handleEncode}
          className="bg-blue-500 text-white p-2 rounded"
        >
          Encode
        </button>
        {encoded && (
          <div>
            <h3 className="font-semibold mt-2">Encoded message:</h3>
            <p className="break-words">{encoded}</p>
          </div>
        )}
      </div>
    </main>
  );
}